<template>
  <div>
    <div id="leixin">
      <div class="search_container">
        <el-form :inline="true" :model="params" class="demo-form-inline">
          <el-form-item label="库存组织" size="mini">
            <el-select clearable v-model="params.company_id" placeholder="请选择" @focus="companyListInvok" @change="companyChange" filterable>
              <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
                <span :class="item.type==1 ? 'optionStyle': ''">{{ item.company_name }}</span>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="业务日期" size="mini">
            <el-date-picker v-model="timeValue" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="供应商(厂家)" size="mini">
            <el-select clearable v-model="params.supplier_id" maxlength="100" :filter-method="supplierChange" @focus="supplierListInvok" @change="changeSuppliter" filterable placeholder="请选择">
              <el-option v-for="item in supplierList" :label="item.supplier_name" :value="item.supplier_id" :key="item.supplier_id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="返胎出库单据号" size="mini">
            <el-input v-model="params.reborn_outstock_sn" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="审核状态" size="mini">
            <el-select clearable v-model="params.auditor_status">
              <el-option label="全部" :value="0">全部</el-option>
              <el-option label="未审核" :value="1">未审核</el-option>
              <el-option label="已审核" :value="2">已审核</el-option>
              <el-option label="作废" :value="3">作废</el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="部门" size="mini">
            <el-select clearable v-model="params.department_id" @focus="DepartmentListInvok" @change="departmentChange" placeholder="请选择" filterable>
              <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="add_delet">
        <el-button icon="el-icon-circle-plus-outline" type="primary" @click="salesAddF" class="addBtn" size="mini" v-if="authority.b_add==1">新增</el-button>
        <el-button plain icon="el-icon-delete" @click="deletes" size="mini" v-if="authority.b_del==1">删除</el-button>
        <el-button @click="audit_enable" size="mini" icon="iconfont icon-pass" type="primary" v-if="authority.b_pass==1">审核</el-button>
        <el-button plain @click="AUDIT_GET" size="mini" icon="iconfont icon-fanshenhe" v-if="authority.b_nopass==1">弃审</el-button>
        <el-button plain @click="AUDIT_LIST" icon="iconfont icon-zuofei" class="addBtn" size="mini" v-if="authority.b_invalid==1">作废</el-button>
      </div>
      <div class="table_container">
        <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" max-height="600" style="width: 100%" @selection-change="handleSelectionChange" v-loading="loading1">
          <el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" width="44">
          </el-table-column>
          <el-table-column label="操作" fixed="right" width="80">
            <template slot-scope="scope">
              <el-button @click="handleEdit(scope, scope.row)" type="text" size="small" v-if="scope.row.auditor_status=='1' && authority.b_edit==1">
                修改
              </el-button>
              <el-button disabled type="text" size="small" v-else>
                修改
              </el-button>
              <el-button type="text" @click="seeDetail(scope, scope.row)" size="small">
                查看
              </el-button>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="company_name" width="150" label="库存组织">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="reborn_outstock_sn" width="180" label="返胎出库单据号">
            <template slot-scope="scope">
              <el-button type="text" @click="seeDetail(scope, scope.row)" size="small">
                {{scope.row.reborn_outstock_sn}}
              </el-button>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="department_name" width="100" label="部门">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="supplier_name" width="100" label="供应商(厂家)">
          </el-table-column>
          <el-table-column align="right" :show-overflow-tooltip="true" property="total_num" width="100" label="返胎数量">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" width="120" label="操作员">
            <template slot-scope="scope">
              <span v-if="scope.row.update_user_name==''">{{scope.row.add_user_name}}</span>
              <span e-else>{{scope.row.update_user_name}}</span>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="auditor_status_name" width="120" label="审核状态">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="auditor_name" width="120" label="审核人">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="business_time" width="120" label="业务日期">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="add_time" width="160" label="单据时间">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="auditor_time" width="160" label="审核时间">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="remark" width="180" label="备注">
          </el-table-column>
        </el-table>
      </div>
      <div class="page_container">
        <div class="pageNumber">每页显示</div>
        <el-pagination class="erp-pagination" @size-change="handleSizeChange" :current-page.sync="goPage" :page-size="pageSize1" @current-change="handleCurrentChange" :page-sizes="per_page" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
        </el-pagination>
      </div>
    </div>

  </div>
</template>
<script>
import { mapActions, mapGetters } from "vuex";
import { getData, opthionList } from "common/utils/public_function/commonUtils";
export default {
  name: "aftersaleAuditreeoutIndex",
  data() {
    return {
      dialogFormVisible: false
    };
  },
  computed: {
    ...mapGetters(["auditreeoutIndex_Get"])
  },
  methods: {
    ...mapActions([
      "auditreeoutAdd",
      "treeoutreeIndexACT",
      "auditreeoutExamine"
    ]),
    // getCompanyFoucs() {
    //   getCompanyListCommon(this); //获取组织
    // },
    // getDepartmentFocus() {
    //   getDepartmentListCommon(this, this.params);
    // },
    companyChange(val) {
      this.$set(this.params, "supplier_id", ""); // 初始化供应商下拉选项
      this.$set(this.params, "department_id", ""); // 初始化部门下拉选项
      if (val) {
        this.params.company_name = this.companyList.filter(
          obj => obj.company_id === val
        )[0].company_name;
      }
      // getDepartmentListCommon(this, this.params, 1);
    },
    departmentChange(val) {
      if (val) {
        this.params.department_name = this.departmentList.filter(
          obj => obj.department_id === val
        )[0].department_name;
      }
    },
    changeSuppliter(val) {
      if (val) {
        this.params.supplier_name = this.supplierList.filter(
          obj => obj.supplier_id === val
        )[0].supplier_name;
      }
    },
    supplierChange(val) {
      // let options = {
      //   company_id: this.params.company_id,
      //   supplier_name: val
      // };
      // supplierListOpen(this, options);
      this.supplierListInvok(val);
    },
    // supplier() {
    //   let options = {
    //     company_id: this.params.company_id,
    //     supplier_name: null
    //   };
    //   supplierListOpen(this, options);
    // },
    handleEdit(scope, row) {
      // //修改三包入库显示与隐藏的方法
      if (row && row.is_execute == 1) {
        this.$message.error("wms系统正在操作此单据，暂不支持单据修改");
        return false;
      }
      this.auditreeoutAdd({
        // moduleName: "auditreeoutAdd",
        reborn_outstock_id: row.reborn_outstock_id,
        params: this.params,
        goPage: this.goPage,
        pageSize: this.pageSize
      });
      this.$router.push({
        path: "/aftersale/treeoutree/auditreeout_Add"
      });
    },
    seeDetail(scope, row) {
      this.auditreeoutExamine({
        // moduleName: "auditreeoutExamine",
        reborn_outstock_id: row.reborn_outstock_id,
        params: this.params,
        goPage: this.goPage,
        pageSize: this.pageSize
      });
      this.$router.push({
        path: "/aftersale/treeoutree/auditreeout_examine"
      });
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    salesAddF() {
      this.auditreeoutAdd({
        // moduleName: "auditreeoutAdd",
        params: this.params,
        goPage: this.goPage,
        pageSize: this.pageSize
      });
      this.$router.push({
        path: "/aftersale/treeoutree/auditreeout_Add"
      });
    },
    onReset() {
      /** 重置时 如需保存默认参数，则： */
      this.$set(this, "params", {
        auditor_status: 0
      });
      this.timeValue = [];
      this.$set(this, "searchParams", {
        params: this.params,
        TableData: this.TableData, //列表数据
        API: "getRebornOutstockList", //接口名
        vm: this //传递的this
      });
      getData(this.searchParams);
      // wz cache
      this.treeoutreeIndexACT({
        params: {},
        goPage: this.goPage,
        pageSize: this.pageSize
      });
    },
    onSearch() {
      if (this.timeValue && this.timeValue.length == 2) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      Object.assign(this.searchParams.params, this.params, {
        page: this.goPage
      });
      getData(this.searchParams);
      // wz cache
      this.treeoutreeIndexACT({
        params: this.params,
        goPage: this.goPage,
        pageSize: this.pageSize
      });
    },
    //删除
    deletes() {
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要删除的信息",
          type: "warning"
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].reborn_outstock_id);
      }
      var rebateArr = addr.toString();
      var deledata = {
        deleID: "reborn_outstock_id",
        deleSelect: this.multipleSelection,
        deleApi: "delRebornOutstock",
        list: {
          UrlApi: "getRebornOutstockList",
          data: this.TableData,
          esData: Object.assign({}, this.params)
        }
      };
      // 校验对接车郎中
      this.$baseData
        .checkCarDoctor({
          bill_type: 16,
          bill_id: rebateArr,
          operation_type: 3
        })
        .then(() => {
          this.$common.deletes(deledata, "删除", this);
        });
    },
    audit_enable() {
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要审核的信息",
          type: "warning"
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].reborn_outstock_id);
      }
      var rebateArr = addr.toString();
      // 校验对接车郎中
      this.$baseData
        .checkCarDoctor({
          bill_type: 16,
          bill_id: rebateArr,
          operation_type: 4
        })
        .then(() => {
          this.$axMethod
            .auditorPassOutstock({
              reborn_outstock_id: rebateArr
            })
            .then(res => {
              if (res.data.code == 200) {
                this.$message({
                  message: res.data.data,
                  type: "success"
                });
                getData(this.searchParams); //获取销售订单列表
              }
            });
          //审核
        });
    },
    AUDIT_GET() {
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要弃审的信息",
          type: "warning"
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].reborn_outstock_id);
      }
      var rebateArr = addr.toString();
      // 校验对接车郎中
      this.$baseData
        .checkCarDoctor({
          bill_type: 16,
          bill_id: rebateArr,
          operation_type: 5
        })
        .then(() => {
          this.$axMethod
            .auditorAgainOutstock({
              reborn_outstock_id: rebateArr
            })
            .then(res => {
              if (res.data.code == 200) {
                this.$message({
                  message: res.data.data,
                  type: "success"
                });
                getData(this.searchParams); //获取销售订单列表
              }
            });
          //弃审
        });
    },
    AUDIT_LIST() {
      //作废
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要作废的信息",
          type: "warning"
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].reborn_outstock_id);
      }
      var rebateArr = addr.toString();
      // 校验对接车郎中
      this.$baseData
        .checkCarDoctor({
          bill_type: 16,
          bill_id: rebateArr,
          operation_type: 6
        })
        .then(() => {
          this.$axMethod
            .auditorInvalidOutstock({
              reborn_outstock_id: rebateArr
            })
            .then(res => {
              if (res.data.code == 200) {
                this.$message({
                  message: res.data.data,
                  type: "success"
                });
                getData(this.searchParams); //获取销售订单列表
              }
            });
        });
    }
  },
  mounted() {
    // wz cache
    let wz_search_params = {};
    this.$set(this, "params", {
      auditor_status: 0
    });
    if (this.auditreeoutIndex_Get && this.auditreeoutIndex_Get.params) {
      if (this.auditreeoutIndex_Get.params.start_time) {
        const { start_time, end_time } = this.auditreeoutIndex_Get.params;
        this.timeValue = this.timeValue.concat([start_time, end_time]);
      }
      this.$set(this, "params", this.auditreeoutIndex_Get.params);
      wz_search_params = this.params;
      opthionList(this, this.auditreeoutIndex_Get.params);
    }
    if (this.auditreeoutIndex_Get && this.auditreeoutIndex_Get.goPage) {
      this.params.page = this.auditreeoutIndex_Get.goPage;
      this.params.per_page = this.auditreeoutIndex_Get.pageSize;
    }
    this.$set(this, "searchParams", {
      params: wz_search_params, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "getRebornOutstockList", //接口名
      vm: this //传递的this
    });
    getData(this.searchParams); //获取销售订单列表
  }
};
</script>

<style scoped>
</style>
